.calculator {
    width: calc(60% - 5px);
    @media (max-width: 550px) {
        width: 100%;
        margin-bottom: 10px;
    }
    background-color: $color4;
    text-align: start;
    padding: 3%;
    form {
        * {
            margin: 2% 0;
        }
        .gender {
            display: flex;
            flex-direction: column;
            label {
                display: flex;
                align-items: center;
                input {
                    margin-right: 4%;
                }
            }
        }
    }
}